import Vue from 'vue'
import App from './App.vue'
//import BootstrapVue from 'bootstrap-vue'

import { ButtonPlugin, FormPlugin, FormFilePlugin, FormInputPlugin, NavbarPlugin, LinkPlugin, CardPlugin, ListGroupPlugin, FormGroupPlugin, ProgressPlugin } from 'bootstrap-vue'
import VueI18n from 'vue-i18n'
import router from './router'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faFolder, faFolderOpen, faFile, faTrashAlt, faArrowAltCircleDown } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import './css/commun.scss'
import './css/custom.scss'

library.add(faFolder, faFolderOpen, faFile, faTrashAlt, faArrowAltCircleDown)

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.use(VueI18n)
Vue.use(ButtonPlugin)
Vue.use(FormPlugin)
Vue.use(NavbarPlugin)
Vue.use(LinkPlugin)
Vue.use(CardPlugin)
Vue.use(FormFilePlugin)
Vue.use(FormInputPlugin)
Vue.use(ProgressPlugin)
Vue.use(ListGroupPlugin)
Vue.use(FormGroupPlugin)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
