<template>
  <div class="row">
    <div class="col-md-12">
      <br />
      <b-card header="Contenu de la carte SD" header-tag="header">
        <div class="bg-default">
          <div class="tree">
            <ul class="mb-1 pl-3 pb-2">
              <tree-item v-for="t in tree" :key="t.key" class="item" :item="t"></tree-item>
            </ul>
          </div>
        </div>
      </b-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import TreeItem from "./TreeItem";

export default {
  name: "SD",
  components: { TreeItem },
  props: {
    msg: String
  },
  data: function() {
    return {
      tree: []
    };
  },
  methods: {
    guid() {
      return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c =>
        (
          c ^
          (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
        ).toString(16)
      );
    }
  },
  computed: {
    treeWithKey: function() {
      return this.tree.map(t => {
        return { t, key: this.guid() };
      });
    }
  },
  mounted: function() {
    axios.get("./config/tree.jso").then(response => {
      this.tree = response.data;
    });
  }
};
</script>

<style scoped>
.tree {
  text-align: left;
  margin: 0;
  padding: 0;
  list-style: none;
}
</style>
