<template>
  <div class="hello">
    <h1>{{ titre }}</h1>
    <h3>{{ env.NODE_ENV }}</h3>
    <p>Liste des vols</p>
    <br />
    <b-list-group>
      <!-- href="encode('/traces/' + f)" -->

      <b-list-group-item v-for="f in traceFiles" :key="f" variant="primary">
        {{f}}
        <button class="btn btn-sm btn-success" @click="downloadFromSD(f)">
          <font-awesome-icon icon="arrow-alt-circle-down" />
        </button>
        <button class="btn btn-sm btn-danger" @click="deleteFromSD(f)">
          <font-awesome-icon icon="trash-alt" />
        </button>
      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Home",
  props: {
    msg: String
  },
  data: function() {
    return {
      titre: "Le titre",
      env: process.env,
      traceFiles: []
    };
  },
  methods: {
    encode: function(uri) {
      return encodeURI(uri);
    },
    downloadFromSD: function(f) {
      alert("telechargement " + f + " soon");
    },
    deleteFromSD: function(f) {
      alert("suppression " + f + "  soon");
    }
  },
  mounted: function() {
    let url = "/flights";
    if (this.env.NODE_ENV == "development") {
      url = "config/flights.jso";
    }
    axios.get(url).then(response => {
      let d = response.data;

      this.traceFiles = d.map(f => {
        return f.name.substring(f.name.lastIndexOf("/") + 1);
      });
    });

    // axios.get("./vols").then(response => {
    //   //let listing = parseDirectoryListing(response.data);
    //   //this.listing = JSON.stringify(response.data);
    //   this.traceFiles = response.data;
    //   //console.log(listing);
    //   //  $("body").append(JSON.stringify(listing));
    // });
  }
};
</script>

