<template>
  <div class="row">
    <div class="col-md-12">
      <div class="zone">
        <p>Upload firmware</p>
        <p>Config param divers</p>
        <p>Config ecran</p>
        <b-card-group deck>
          <b-card
            header="Upload firmware"
            header-tag="header"
            footer="Mise à jour du GNU Vario"
            footer-tag="footer"
            title="Téléversement"
          >
            <b-form @submit="onSubmitFirmware" @reset="onResetFirmware">
              <b-form-group
                id="input-group-firmware"
                label-cols-sm="12"
                label="Veuillez sélectionner le fichier du firmware à envoyer sur le vario:"
                label-for="input-firmware"
                description="au format .hex"
              >
                <b-form-file id="input-firmware" v-model="file" :state="Boolean(file)" plain></b-form-file>
                 <b-progress :value="percentCompleted" max="100" show-progress animated></b-progress>
              </b-form-group>
              <b-form-group
                id="input-group-path"
                label-cols-sm="12"
                label-for="input-path"
                description="chemin"
              >
                <b-form-input id="input-path" v-model="path" placeholder="Chemin + nom du fichier"></b-form-input>
              </b-form-group>
              <b-button variant="primary" type="submit">Envoyer</b-button>
            </b-form>
          </b-card>
          <b-card
            header="Upload firmware"
            header-tag="header"
            footer="Mise à jour du GNU Vario"
            footer-tag="footer"
            title="Téléversement"
          >
            <b-link :to="{ name: 'screen'}">Ecran</b-link>
          </b-card>
        </b-card-group>
      </div>
    </div>
  </div>
</template>


<script>
import axios from "axios";
export default {
  name: "Configuration",
  props: {
    msg: String
  },
  data: function() {
    return {
      file: null,
      show: false,
      percentCompleted: null,
      path: null
    };
  },
  methods: {
    onSubmitFirmware: function() {
      let formData = new FormData();
      formData.append("data", this.file, this.path);
      var self = this;
      var config = {
        headers: {
          "Content-Type": "multipart/form-data"
        },
        onUploadProgress: function(progressEvent) {
          self.percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
        }
      };

      axios
        .post("/upload", formData, config)
        .then(function() {
          console.log("SUCCESS!!");
        })
        .catch(function() {
          console.log("FAILURE!!");
        });
      return;
    },
    onResetFirmware: function() {
      this.file = null;
      return;
    }
  }
};
</script>

